let baseUrl = ''; //请求发送的域名
let routerMode = 'hash';


if (process.env.NODE_ENV == 'development') {
    baseUrl = 'http://localhost:8081/';

}else if(process.env.NODE_ENV == 'production'){
    baseUrl = '';
}

export {
	baseUrl,
	routerMode,
}


