import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'
import getters from './getters'

Vue.use(Vuex)

const state = {
	loading:false,
	userInfo:'',
	newPro:{
		name:'',
		number:'',
		address:'',
		yezhu:'',
		design:'',
		danwei:'',
		mdanwei:'',
		introduce:'',
		members:[]
	},
	newOrder:{
		type:0,
	}
}

export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations
})


